var openid = uni.getStorageSync('openid');

export default {
	components: {

	},
	data() {
		return {
			list:[],
			user:[],
			type:"",
			zixun_num:0,
			jianyi_num:0,
			tousu_num:0
		}
	},
	methods: {
		gotoDetail(i){
		let url = "/pages/question/question?id="+i;
			uni.navigateTo({
				url: url
			})
		},
	},
	// onLoad相当于created生命周期
	onLoad() {	
			this.req.request('/list/'+openid,{
				"spare4":"-1"
			},'POST',(res) =>{
				if(res.success){
				console.log(res)
				this.list = res.message
					this.list.map(it=>{
						this.type = it.wentiType
						if(this.type=='1')this.zixun_num++;
						else if(this.type=='2')this.jianyi_num++;
						else if(this.type=='3')this.tousu_num++
						
					})
				this.user = res.nameAndAvator
				
				
				}else{
					uni.showModal({
						title: '提示',
						content: "请先登录吧！",
						showCancel: false,
						success: function() {
							uni.navigateTo({
								url: ""
							});
						}
					})
				}
				
		
			},function(e) {
				uni.showToast({
					title: e.errMsg,
					mask: true,
					duration: 1200,
					icon: 'none'
				});
			});			
	
	}

}
